import React, { Component, PropTypes } from 'react';
import Header from './Header';
import {sections} from './Header';
import Grid from '@material-ui/core/Grid';
import { withStyles, createStyled } from '@material-ui/core/styles';
import classNames from 'classnames';
import AboutMe from './AboutMe';
import Home from './Home';
import Portfolio from './Portfolio';
import Footer from './Footer';
import Experience from './Experience';
import Education from './Education';
import Contact from './Contact';
import Skill from './Skill';
import Particles from 'react-particles-js';
import {particlesConfig} from '../data/particlesConfig';

const styles = theme => ({
  sections:{
    padding: '60px 40px;',
    minHeight: '700px'

  },
  particleBg: {
    margin: '-120px -40px -40px'
  },
  home: {
    backgroundColor: theme.palette.text.primary,
    position: 'relative',
    display: 'table',
    width: '100%'
  },
  aboutme: {
    position: 'relative',
    display: 'table',
    width: '100%',
    backgroundColor: theme.palette.secondary.main
  },
  experience:{
    backgroundColor: "#db2c4c"
  },
  projects:{
    backgroundColor: theme.palette.primary.main
  },
  education: {
    backgroundColor:  "#a3a31e"
  },
  contact: {
    backgroundColor: theme.palette.secondary.main
  },
  fixedWidthMobile: {
    [theme.breakpoints.down('md')]: {
      maxWidth: '100%',
    }
  }
});


/**
* Landing
*/
class Landing extends Component { // eslint-disable-line react/prefer-stateless-function
  constructor(props) {
    super(props);
    this.state = {
      currentPage: window.location.hash ? window.location.hash : sections[0].tag
    };
    /*window.addEventListener('scroll', this.handleScroll);
    this.sections = sections;
    setTimeout(function(){
    sections.forEach((section) => {
    section['position'] = window.document.getElementById(section.tag.split('#')[1]).offsetTop
  });
  this.handleScroll();
}.bind(this), 3000)*/
}

changePage = (value) => {
  this.setState({currentPage: value, mobileOpen: false });
  window.location.hash = value;
};

/*handleScroll = () => {
if((section.position -250) > document.documentElement.scrollTop){
this.changePage(i > 0 ? sections[i - 1].tag : sections[0].tag);
throw {};
}
});
}*/


render() {
  const { classes } = this.props;
  return (
    <React.Fragment>
      <Header changePage={this.changePage} currentPage={this.state.currentPage}/>
      <main id="main">
        <div className={classNames(classes.sections, classes.home)} id="home">
          <Particles id="particleBg" params={particlesConfig} className={classes.particleBg}/>
          <Home />
        </div>
        <div className={classNames(classes.sections, classes.aboutme)} id="aboutme">
          <AboutMe changePage={this.changePage}/>
        </div>
        <div className={classNames(classes.sections, classes.experience)} id="experience">
          <Grid container spacing={24} >
            <Grid item sm={12} md={6}>
              <Experience/>
            </Grid>
            <Grid item sm={12} md={6}  className={classes.fixedWidthMobile}>
              <Skill/>
            </Grid>
          </Grid>
        </div>
        <div className={classNames(classes.sections, classes.projects)} id="projects">
          <Portfolio/>
        </div>
        <div className={classNames(classes.sections, classes.education)} id="education">
          <Education/>
        </div>
        <div className={classNames(classes.sections, classes.contact)} id="contact" style={{'minHeight': 700}}>
          <Contact/>
        </div>
      </main>
      <Footer />
    </React.Fragment>
  );
}
}

export default withStyles(styles, { withTheme: true })(Landing);



